import classNames from "classnames";
import { Filters } from "../Filters/Filters";
import { Toolbar } from "../Toolbar/Toolbar";
import { Grid } from "../Grid/Grid";
import { Table } from "../Table/Table";
import React from "react";
import { useStyles } from "./Content.styles";

export const Content = (props) => {
  const classes = useStyles();

  return (
    <div className={classNames('container', classes.content)}>
      <div className={'row'}>
        <div className={'col'}>
          <Filters
            onFiltersUpdate={props.onFiltersUpdate}
            initial={props.filters}
            options={props.filtersOptions}
          />
          {props.data && !props.loading && !props.error && (
            <>
              <Toolbar count={props.data.length} view={props.view} onViewChange={props.onViewChange} />
              {props.view === 'grid'
                ? <Grid region={props.filters.region} productions={props.data}/>
                : <Table region={props.filters.region} productions={props.data}/>}
            </>
          )}
          {props.loading && (
            <div className={classes.loader}>
              <i className="fas fa-plus fa-spin fa-5x" />
            </div>
          )}
          {props.error && (
            <div className={classes.error}>
              <i className="fas fa-sad-cry fa-5x" />
              Error
            </div>
          )}
        </div>
      </div>
    </div>
  );
};
