import ExpansionPanelSummary from "@material-ui/core/ExpansionPanelSummary";
import { ExpandMore } from "@material-ui/icons";
import { ExpansionPanelDetails, Typography } from "@material-ui/core";
import ExpansionPanel from "@material-ui/core/ExpansionPanel";
import React from "react";
import { Episodes } from "../Episodes/Episodes";
import { useSeasonsStyles } from "./Seasons.styles";

export const Seasons = ({ seasons }) => {
  const classes = useSeasonsStyles();

  return (
    <div>
      {seasons.map((season, index) => (
        <ExpansionPanel square={true} key={index}>
          <ExpansionPanelSummary expandIcon={<ExpandMore />}>
            <Typography>
              Season {season.seasonNumber}{' '} [{season.releaseYear}]
            </Typography>
          </ExpansionPanelSummary>
          <ExpansionPanelDetails classes={{ root: classes.detailsRoot }}>
            <Episodes episodes={season.episodes} />
          </ExpansionPanelDetails>
        </ExpansionPanel>
      ))}
    </div>
  );
};
