import React, { useEffect, useState } from 'react';
import { Paper } from "@material-ui/core";
import { Waypoint } from "react-waypoint";
import MUIDataTable from "mui-datatables";
import { renderList } from "../../common/table/renderList";
import { makePath } from "../../../utils/makePath";
import { ROUTE_DETAILS } from "../../../config/routes";
import { useHistory } from "react-router";
import { useTableStyles } from "./Table.styles";

const PAGE_SIZE = 24;

export const Table = ({productions, region}) => {
  const [page, setPage] = useState(2);
  const history = useHistory();
  const classes = useTableStyles();

  useEffect(() => {
    setPage(2);
  }, [productions]);

  const showMoreHandler = () => {
    const maxPages = productions.length ? productions.length / PAGE_SIZE : 2;
    setPage(Math.min(page + 1, maxPages));
  };

  const columns = [
    {
      name: 'title',
      label: 'Title',
      options: {
        customBodyRender: (value) => {
          return <strong>{value}</strong>;
        }
      }
    },
    {
      name: 'genres',
      label: 'Genres',
      options: {
        customBodyRender: renderList,
      },
    },
    {
      name: 'tracks',
      label: 'Audio tracks',
      options: {
        customBodyRender: renderList,
      },
    },
    {
      name: 'captions',
      label: 'Captions',
      options: {
        customBodyRender: renderList,
      },
    },
    {
      name: 'format',
      label: 'Format',
      options: {
        customBodyRender: (values) => values.join('/'),
      },
    },
  ];

  const rows = productions.slice(0, PAGE_SIZE * page);

  const openDetails = (production) => {
    history.push(makePath(ROUTE_DETAILS, { region, id: production.id }));
  };

  return (
    <Paper className={'mt-3'}>
      <MUIDataTable
        data={rows}
        columns={columns}
        classes={classes}
        options={{
          sort: false,
          filter: false,
          search: false,
          print: false,
          download: false,
          pagination: false,
          selectableRows: 'none',
          viewColumns: false,
          elevation: 0,
          responsive: 'stacked',
          rowHover: false,
          onRowClick: (data, meta) => openDetails(productions[meta.rowIndex]),
        }}
      />
      <Waypoint
        onEnter={showMoreHandler}
        bottomOffset={-1000}
      />
    </Paper>
  );
};
