const getVideoUrl = (contentId) => `https://www.disneyplus.com/video/${contentId}`;
const getSeriesUrl = (id) => `https://www.disneyplus.com/series/_/${id}`;

export const getDisneyPlusLink = (production) => {
  if (production.type === 'movie') {
    return getVideoUrl(production.contentId);
  }
  if (production.type === 'series') {
    return getSeriesUrl(production.id);
  }
};
