import { Route, Switch } from "react-router";
import { ROUTE_SEARCH_GRID, ROUTE_SEARCH_TABLE } from "../../config/routes";
import React from "react";
import { ContentContainer } from "./Content/Content.container";
import qs from 'query-string';
import { pickBy } from "lodash";
import { isDefaultFilterValue } from "./Content/filtering/initialFilters";

const cleanSearchParams = (filters) => {
  return pickBy(filters, (value, key) => {
    return !isDefaultFilterValue(key, value);
  });
};

export const Search = (props) => {
  const searchUpdateHandler = ({ view, filters }) => {
    const route = view === 'grid' ? ROUTE_SEARCH_GRID : ROUTE_SEARCH_TABLE;
    const params = qs.stringify(cleanSearchParams(filters));

    props.history.push(`${route}?${params}`);
  };

  console.log(props.location.search);

  const filters = qs.parse(props.location.search) || {};

  return (
    <Switch>
      <Route exact path={ROUTE_SEARCH_GRID}>
        <ContentContainer view="grid" filters={filters} onUpdate={searchUpdateHandler} />
      </Route>
      <Route exact path={ROUTE_SEARCH_TABLE}>
        <ContentContainer view="list" filters={filters} onUpdate={searchUpdateHandler} />
      </Route>
    </Switch>
  );
};
