import React from "react";
import { useStyles } from "./Production.styles";
import { Paper, Typography } from "@material-ui/core";
import Chip from "@material-ui/core/Chip";
import { makePath } from "../../utils/makePath";
import { ROUTE_SEARCH_GRID } from "../../config/routes";
import { useHistory } from "react-router";
import { getSlug } from "../Search/Content/filtering/filterData";
import { Block } from "./Block/Block";
import { Seasons } from "./Seasons/Seasons";
import { formatDuration } from "../../utils/formatDuration";
import { DetailsRow } from "./DetailsRow/DetailsRow";
import { More } from "./More/More";
import { PlayArrow } from '@material-ui/icons';
import Button from "@material-ui/core/Button";
import { getDisneyPlusLink } from "../../utils/getDisneyPlusLink";
import Divider from '@material-ui/core/Divider';

export const Production = (props) => {
  const {production, region} = props;
  const classes = useStyles({backdrop: production ? production.backdrop : ''});
  const history = useHistory();

  const searchFor = (filters) => {
    history.push(makePath(ROUTE_SEARCH_GRID, null, {region, ...filters}));
  };

  const chipRender = (value, filter, index) => {
    return (
      <Chip
        label={value}
        key={index}
        onClick={() => searchFor({[filter]: getSlug(value)})}
        className={'mr-1 mb-1'}
      />
    )
  };

  const movieDetailsRender = () => (
    <>
      <DetailsRow label={'Release year'}>
        {production.releaseYear}
      </DetailsRow>
      <DetailsRow label={'Duration'}>
        {formatDuration(production.duration)}
      </DetailsRow>
      <DetailsRow label={'Format'}>
        {production.format}
      </DetailsRow>
    </>
  );

  const seriesDetailsRender = () => (
    <>
      <DetailsRow label={'Release year'}>
        {production.releaseYear}
      </DetailsRow>
      <DetailsRow label={'Seasons'}>
        {production.seasonsNumber}
      </DetailsRow>
      <DetailsRow label={'Episodes in total'}>
        {production.episodesNumber}
      </DetailsRow>
      <DetailsRow label={'Formats'}>
        {production.format.join('/')}
      </DetailsRow>
    </>
  );

  const teamRender = ({ actors, creators }) => (
    <>
      {actors.map((actor) => (
        <DetailsRow label={actor.name}>
          {actor.character}
        </DetailsRow>
      ))}
      {actors.length && creators.length && <Divider className={'my-3'} />}
      {creators.map((actor) => (
        <DetailsRow label={actor.name}>
          {actor.role}
        </DetailsRow>
      ))}
    </>
  );

  if (props.loading) {
    return (
      <div className={classes.container}>
        <div className={classes.loader}>
          <i className="fas fa-plus fa-spin fa-5x"/>
        </div>
      </div>
    );
  }

  if (!production || props.error) {
    return (
      <div className={classes.container}>
        <div className={classes.error}>
          <i className="fas fa-sad-cry fa-5x"/>
          Error
        </div>
      </div>
    );
  }

  return (
    <div className={classes.container}>
      <div className={classes.backdrop}>
        <div className={'container'}>
          <Typography variant={"h1"} className={'mb-2'} res>
            {production.title}
          </Typography>
          <Typography variant={"subtitle1"}>
            {production.description}
          </Typography>
        </div>
      </div>
      <div className={'container'}>
        <div className={'row d-flex'}>
          <div className={'col-12 col-lg-9 order-2 order-lg-1'}>

            <Block title={'Details'}>
              <Paper elevation={1} square={true} className={'py-3 px-4 mb-4'}>
                {production.type === 'movie' && movieDetailsRender()}
                {production.type === 'series' && seriesDetailsRender()}
              </Paper>
            </Block>

            <Block title={'Cast and creators'}>
              <Paper elevation={1} square={true} className={'py-3 px-4 mb-4'}>
                {teamRender(production)}
              </Paper>
            </Block>

            {production.seasons && (
              <Block title={'Seasons and Episodes'}>
                <Seasons seasons={production.seasons}/>
              </Block>
            )}

            <Block title={'More'}>
              <More extras={production.extras} related={production.related} region={region}/>
            </Block>
          </div>

          <div className={'col-12 col-lg-3  order-1 order-lg-2'}>
            <div className={classes.play}>
              <Button
                color="primary"
                size={"large"}
                variant="contained"
                fullWidth={true}
                component={'a'}
                target={'_blank'}
                href={getDisneyPlusLink(production)}
              >
                <PlayArrow className={'mr-2'} />
                Play on Disney+
              </Button>
            </div>

            <Block title={'Genres'} smaller>
              {production.genres.map((genre, index) => {
                return chipRender(genre, 'genre', index);
              })}
            </Block>

            <Block title={'Audio tracks'} smaller>
              {production.tracks.map((track, index) => {
                return chipRender(track, 'track', index);
              })}
            </Block>

            <Block title={'Subtitles'} smaller>
              {production.captions.map((captions, index) => {
                return chipRender(captions, 'captions', index);
              })}
            </Block>
          </div>
        </div>
      </div>
    </div>
  );
};
