import React, { useEffect, useState } from 'react';
import { Card } from "./Card/Card";
import { useStyles } from "./Grid.styles";
import { Waypoint } from 'react-waypoint';

const PAGE_SIZE = 16;

export const Grid = ({productions, region}) => {
  const classes = useStyles();
  const [page, setPage] = useState(2);

  useEffect(() => {
    setPage(2);
  }, [productions]);

  const showMoreHandler = () => {
    const maxPages = productions.length ? productions.length / PAGE_SIZE : 2;
    setPage(Math.min(page + 1, maxPages));
  };

  if (productions.length === 0) {
    return (
      <div className={classes.empty}>
        <i className="fas fa-sad-tear fa-5x" />
        Nothing found
      </div>
    );
  }

  return (
    <div className={classes.grid}>
      {productions.slice(0, PAGE_SIZE * page).map((item, index) => {
        return <Card production={item} region={region} key={index} />;
      })}
      <Waypoint
        onEnter={showMoreHandler}
        bottomOffset={-1000}
      />
    </div>
  );
};
