import MUIDataTable from "mui-datatables";
import React from "react";
import { renderList } from "../../common/table/renderList";

export const Episodes = ({ episodes }) => {
  const columns = [
    {
      name: 'episodeNumber',
      label: 'No',
      options: {
        customBodyRender: (value) => {
          return <strong>{value}</strong>;
        },
        filter: false,
      }
    },
    {
      name: 'title',
      label: 'Title',
      options: {
        customBodyRender: (value) => {
          return <strong>{value}</strong>;
        },
        filter: false,
        viewColumns: false,
      }
    },
    {
      name: 'tracks',
      label: 'Audio',
      options: {
        customBodyRender: renderList,
      },
    },
    {
      name: 'captions',
      label: 'Captions',
      options: {
        customBodyRender: renderList,
      },
    },
    {
      name: 'releaseYear',
      label: 'Release',
      options: {
        display: false,
      },
    },
    {
      name: 'format',
      label: 'Format',
    },
  ];

  return (
    <MUIDataTable
      data={episodes}
      columns={columns}
      options={{
        textLabels: {
          body: {
            noMatch: "Sorry, no episodes found",
          }
        },
        sort: false,
        filter: true,
        search: true,
        searchPlaceholder: 'What you want to search?',
        print: false,
        download: false,
        pagination: false,
        selectableRows: 'none',
        viewColumns: true,
        elevation: 0,
        responsive: 'stacked',
        rowHover: false,
      }}
    />
  )
};
