import React from "react";
import { useData } from "../../hooks/useData";
import { Production } from "./Production";

export const ProductionContainer = (props) => {
  const { region, id } = props.match.params;
  const { data, error, loading } = useData(region,'productions');

  const production = data ? data.find((title) => title.id === id) : null;

  return <Production production={production} loading={loading} error={error} region={region} />;
};
