import { formatDuration } from "./formatDuration";

export const getProductionShortDetails = (item) => {
  if(item.type === 'movie') {
    return `${item.format || '??'}, ${formatDuration(item.duration)}`;
  } else {
    return `${item.format.join('/') || '??'}, ${item.seasonsNumber} seasons`;
  }
};

export const getProductionDetails = (item) => {
  if(item.type === 'movie') {
    return `
      ${item.type.toUpperCase()} /
      ${item.releaseYear} /
      ${formatDuration(item.duration)} /
      ${item.format || '(format unknown)'} 
    `;
  } else {
    return `
      ${item.type.toUpperCase()} /
      ${item.releaseYear} /
      ${item.seasonsNumber} seasons / 
      ${item.episodesNumber} episodes /
      ${item.format.join(', ') || '(format unknown)'}
    `;
  }
};
