import CardMedia from "@material-ui/core/CardMedia";
import CardContent from "@material-ui/core/CardContent";
import Typography from "@material-ui/core/Typography";
import MuiCard from '@material-ui/core/Card';
import React from "react";
import { useStyles } from "./Card.styles";
import { CardHeader } from "@material-ui/core";
import Avatar from '@material-ui/core/Avatar';
import TheatersIcon from '@material-ui/icons/Theaters';
import TvIcon from '@material-ui/icons/Tv';
import { getBackdropUrl } from "../../../../utils/getBackdropUrl";
import CardActionArea from "@material-ui/core/CardActionArea";
import { useHistory } from "react-router";
import { ROUTE_DETAILS } from "../../../../config/routes";
import { makePath } from "../../../../utils/makePath";
import { getProductionShortDetails } from "../../../../utils/productions";

export const Card = ({production, region}) => {
  const classes = useStyles();
  const history = useHistory();

  const openDetails = () => {
    history.push(makePath(ROUTE_DETAILS, { region, id: production.id }));
  };

  return (
    <MuiCard>
      <CardActionArea className={classes.actionArea} onClick={openDetails}>
        <CardHeader
          avatar={
            <Avatar className={classes[`${production.type}Type`]}>
              {production.type === 'movie' ? <TheatersIcon/> : <TvIcon/> }
            </Avatar>
          }
          title={production.genres[0]}
          subheader={getProductionShortDetails(production)}
        />
        <CardMedia
          image={getBackdropUrl(production.backdrop)}
          title={production.title}
          className={classes.media}
        />
        <CardContent>
          <Typography gutterBottom variant="subtitle1" component="h2">
            <strong>{production.title}</strong>
          </Typography>
          <Typography variant="body2" color="textSecondary" component="p">
            {production.description}
          </Typography>
        </CardContent>
      </CardActionArea>
    </MuiCard>
  );
};
