import { useEffect, useState } from "react";
import { format } from 'date-fns'

const cache = {};

const setCache = ([path, data]) => {
  cache[path] = data;
};

export const useData = (prefix, ...file) => {
  const cacheBusterDate = format(new Date(), 'yyyy-MM-dd');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(false);
  const path = `/data/${prefix}/${[...file].join('/')}.json?${cacheBusterDate}`;

  useEffect(() => {
    setError(false);
    setLoading(true);
    (async () => {
      try {
        if (!cache[path]) {
          const response = await fetch(path);
          const data = await response.json();
          setCache([path, data]);
        }
        setLoading(false);
      } catch (e) {
        setError(true)
      }
    })();
  }, [path]);

  return { data: cache[path], error, loading };
};
