import React, { useEffect, useMemo, useState } from "react";
import { useData } from "../../../hooks/useData";
import { getFiltersOptions } from "./filtering/getFiltersOptions";
import { filterData } from "./filtering/filterData";
import { Content } from "./Content";
import { initialFilters } from "./filtering/initialFilters";

export const ContentContainer = (props) => {
  const [view, setView] = useState(props.view);
  const [filters, setFilters] = useState({ ...initialFilters, ...props.filters });
  const { data, error, loading } = useData(filters.region,'productions');

  useEffect(() => {
    setFilters({ ...initialFilters, ...props.filters });
  }, [props.filters]);

  const filtersUpdateHandler = (values) => {
    setFilters(values);
    props.onUpdate({ view, filters: values });
  };

  const viewChangeHandler = (viewType) => {
    setView(viewType);
    props.onUpdate({ view: viewType, filters });
  };

  const filtersOptions = useMemo(() => {
    return data ? getFiltersOptions(data) : getFiltersOptions([]);
  }, [data]);

  const filtered = useMemo(() => {
    return data ? filterData(data, filters) : [];
  }, [data, filters]);

  return (
    <Content
      data={filtered}
      loading={loading}
      error={error}
      filters={filters}
      filtersOptions={filtersOptions}
      onFiltersUpdate={filtersUpdateHandler}
      view={view}
      onViewChange={viewChangeHandler}
    />
  );
};
