import _ from 'lodash';
import { getSlug } from "./filterData";

const buildOptions = (data, field, all, sort = true) => {
  return _(data)
    .map((items) => items[field])
    .flatten()
    .uniq()
    .map(item => ({ value: getSlug(item), label: item }))
    .sortBy(sort ? ['label'] : [])
    .unshift({value: 'all', label: all})
    .value();
};


export const getFiltersOptions = (data) => {
  const type = [
    {value: 'all', label: 'All types'},
    {value: 'movie', label: 'Movies'},
    {value: 'series', label: 'Series'},
  ];

  const region = [
    {value: 'nl', label: 'Netherlands'},
    {value: 'us', label: 'USA'},
    // {value: 'au', label: 'Australia'},
  ];

  const track = buildOptions(data, 'tracks', 'All tracks');
  const captions = buildOptions(data, 'captions', 'All captions');
  const genre = buildOptions(data, 'genres', 'All genres');
  const format = buildOptions(data, 'format', 'Every format', false);

  return {
    type,
    track,
    genre,
    captions,
    format,
    region,
  };
};
