import { isDefaultFilterValue } from "./initialFilters";
import slugify from 'slugify';

export const getSlug = (text) => {
  return slugify(text, {remove: /[*+~.()[\]'"!:@]/g})
};

const filterByStringPart = (data, field, value) => {
  return isDefaultFilterValue(field, value)
    ? data
    : data.filter(item => {
      return item[field].toLowerCase().includes(value.toLowerCase());
    });
};

const filterByArrays = (data, field, value, filterKey = field) => {
  return isDefaultFilterValue(filterKey, value)
    ? data
    : data.filter(item => {
      return item[field]
        .map(value => getSlug(value))
        .includes(value)
    });
};

const filterByString = (data, field, value, filterKey = field) => {
  return isDefaultFilterValue(filterKey, value)
    ? data
    : data.filter(item => {
      return getSlug(item[field]) === value;
    });
};

export const filterData = (data, filters) => {
  let results = [...data || []];

  results = filterByStringPart(results, 'title', filters.query, 'query');
  results = filterByString(results, 'type', filters.type);
  results = filterByArrays(results, 'genres', filters.genre, 'genre');
  results = filterByArrays(results, 'tracks', filters.track, 'track');
  results = filterByArrays(results, 'captions', filters.captions);
  results = filterByArrays(results, 'format', filters.format);

  return results;
};
