import MUIDataTable from "mui-datatables";
import React from "react";
import Button from "@material-ui/core/Button";
import { makePath } from "../../../utils/makePath";
import { ROUTE_DETAILS_BY_TITLE } from "../../../config/routes";
import { Link } from "react-router-dom";
import { useRelatedStyles } from "./Related.styles";

export const Related = ({ related, region }) => {
  const classes = useRelatedStyles();

  const columns = [
    {
      name: 'title',
      label: 'Title',
      options: {
        customBodyRender: (value) => {
          return <strong>{value}</strong>;
        },
      }
    },
    {
      name: 'releaseYear',
      label: 'Release Year',
    },
    {
      name: 'button',
      label: ' ',
      options: {
        setCellProps: () => ({ className: classes.buttonCell }),
        customBodyRender: (value, { rowIndex }) => {
          const production = related[rowIndex];
          return (
            <Button
              component={Link}
              to={makePath(ROUTE_DETAILS_BY_TITLE, { region, ...production })}
              size={"small"}
              variant={"outlined"}>
              More details
            </Button>
          )
        },
      },
    },
  ];

  return (
    <MUIDataTable
      data={related}
      columns={columns}
      className={'pt-2'}
      options={{
        textLabels: {
          body: {
            noMatch: "Sorry, no related found",
          }
        },
        sort: false,
        search: false,
        filter: false,
        searchPlaceholder: 'What you want to search?',
        print: false,
        download: false,
        pagination: false,
        selectableRows: 'none',
        viewColumns: false,
        elevation: 0,
        responsive: 'stacked',
        rowHover: false,
      }}
    />
  )
};
