import { useData } from "../../hooks/useData";
import React, { useEffect } from "react";
import { useStyles } from "./Production.styles";
import { useHistory } from "react-router";
import { makePath } from "../../utils/makePath";
import { ROUTE_DETAILS } from "../../config/routes";

export const RedirectTitle = (props) => {
  const { region, title, releaseYear } = props.match.params;
  const { data, error, loading } = useData(region,'productions');
  const classes = useStyles({backdrop: ''});
  const history = useHistory();

  useEffect(() => {
    if (data && !error && !loading) {
      const production = data.find((production) => {
        return production.title === decodeURIComponent(title) && production.releaseYear === parseInt(releaseYear);
      });

      if (production) {
        history.push(makePath(ROUTE_DETAILS, {region, id: production.id}));
      }
    }
  }, [history, data, loading, error, releaseYear, title, region]);

  return (
    <div className={classes.container}>
      <div className={classes.loader}>
        <i className="fas fa-plus fa-spin fa-5x"/>
      </div>
    </div>
  );
};
