import React from 'react';
import { Formik } from "formik";
import { Select } from "../../common/form/Select/Select";
import { Text } from "../../common/form/Text/Text";
import { Search } from "@material-ui/icons";
import { colors } from "@material-ui/core";
import { useStyles } from "./Filters.styles";
import { debounce } from "lodash";

export const Form = (props) => {
  const classes = useStyles();

  const keyPressHandler = (event) => {
    if (event.key === "Enter") {
      document.activeElement.blur();
    }
  };

  const handleFormSubmit = debounce((filters) => {
    props.onFiltersUpdate(filters);
  }, 500);

  console.log('props.initial', props.initial);

  return (
    <Formik initialValues={props.initial} onSubmit={handleFormSubmit} enableReinitialize={true}>
      {({handleChange, handleSubmit, submitForm}) => (
        <form onSubmit={handleSubmit}>
          <div className={'row'}>
            <div className={'col-12'}>
              <div className={classes.search}>
                <div className={classes.searchIcon}>
                  <Search style={{ color: colors.grey[600] }} fontSize={"large"} />
                </div>
                <Text
                  label={''}
                  placeholder="Search..."
                  name={'query'}
                  variant="outlined"
                  onChange={(e) => {
                    handleChange(e);
                    submitForm();
                  }}
                  onKeyPress={(event) => keyPressHandler(event)}
                  InputProps={{ className: classes.searchField }}
                  inputProps={{ className: classes.searchInput }}
                />
              </div>
            </div>
          </div>
          <div className={`row ${classes.selectsRow}`}>
            <div className={'col-12 col-lg-6'}>
              <div className={'row'}>
                <Select
                  name={'type'}
                  label={'Type'}
                  className={'col-md-4 pr-md-2'}
                  options={props.options.type}
                  onChange={(e) => {
                    handleChange(e);
                    submitForm();
                  }}
                />
                <Select
                  name={'genre'}
                  label={'Genre'}
                  className={'col-md-4 px-md-2'}
                  options={props.options.genre}
                  onChange={(e) => {
                    handleChange(e);
                    submitForm();
                  }}
                />
                <Select
                  name={'track'}
                  label={'Audio track'}
                  className={'col-md-4 pl-md-2 pr-lg-2'}
                  options={props.options.track}
                  onChange={(e) => {
                    handleChange(e);
                    submitForm();
                  }}
                />
              </div>
            </div>
            <div className={'col-12 col-lg-6'}>
              <div className={'row'}>
                <Select
                  name={'captions'}
                  label={'Captions'}
                  className={'col-md-4 pr-md-2 pl-lg-2'}
                  options={props.options.captions}
                  onChange={(e) => {
                    handleChange(e);
                    submitForm();
                  }}
                />
                <Select
                  name={'format'}
                  label={'Format'}
                  className={'col-md-4 px-md-2'}
                  options={props.options.format}
                  onChange={(e) => {
                    handleChange(e);
                    submitForm();
                  }}
                />
                <Select
                  name={'region'}
                  label={'Region'}
                  className={'col-md-4 pl-md-2'}
                  options={props.options.region}
                  onChange={(e) => {
                    handleChange(e);
                    submitForm();
                  }}
                />
              </div>
            </div>
          </div>
        </form>
      )}
    </Formik>
  );
};
