import React, { useEffect, useState } from 'react';
import { CssBaseline } from "@material-ui/core";
import { useStyles } from "./App.styles";
import { ThemeProvider } from '@material-ui/core/styles';
import { theme } from "../styles/theme";
import { TopBar } from "./layout/TopBar/TopBar";
import 'bootstrap-css-only/css/bootstrap.min.css';
import '@fortawesome/fontawesome-free/css/all.css';
import { Footer } from "./layout/Footer/Footer";
import { IntlProvider } from "react-intl";
import { en } from "../i18n/en";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import { ROUTE_DETAILS, ROUTE_DETAILS_BY_TITLE, ROUTE_SEARCH } from "../config/routes";
import { Search } from "./Search/Search";
import { ProductionContainer } from "./Production/ProductionContainer";
import { ScrollToTop } from "./common/navigation/ScrollToTop";
import { RedirectTitle } from "./Production/RedirectTitle";

function App() {
  const classes = useStyles();
  const [, setQuery] = useState('');

  useEffect(() => {
    // window.gtag('config', process.env.GOOGLE_ANALYTICS);
  }, []);

  return (
    <IntlProvider locale={'pl'} messages={en}>
      <ThemeProvider theme={theme}>
        <div className={classes.container}>
          <Router>
            <ScrollToTop />
            <CssBaseline/>
            <div className={classes.website}>
              <TopBar onQueryUpdate={setQuery} />
              <Switch>
                <Route path={ROUTE_DETAILS} component={ProductionContainer} />
                <Route path={ROUTE_DETAILS_BY_TITLE} component={RedirectTitle} />
                <Route path={ROUTE_SEARCH} component={Search} />
              </Switch>
              <Footer/>
            </div>
          </Router>
        </div>
      </ThemeProvider>
    </IntlProvider>
  );
}

export default App;
