import Card from "@material-ui/core/Card";
import IconButton from "@material-ui/core/IconButton";
import AppsIcon from "@material-ui/icons/Apps";
import React from "react";
import { Typography } from "@material-ui/core";
import Reorder from "@material-ui/icons/Reorder";
import { useStyles } from "./Toolbar.styles";
import classNames from 'classnames';
import { injectIntl } from "react-intl";

export const Toolbar = injectIntl((props) => {
  const classes = useStyles();

  const changeView = (view) => {
    props.onViewChange(view);
  };

  return (
    <Card className={classNames('py-1 px-2 px-4', classes.container)}>
      <Typography component={'span'} className={classes.results}>
        {props.intl.formatMessage({ id: 'RESULTS_LIST' }, {count: props.count})}
      </Typography>
      <Typography component={'span'} className="mr-2 d-none d-sm-none d-md-inline">
        View
      </Typography>
      <IconButton onClick={() => changeView('grid')}>
        <AppsIcon className={classNames(classes.icon, { [classes.iconActive]: props.view === 'grid' })} />
      </IconButton>
      <IconButton onClick={() => changeView('list')}>
        <Reorder className={classNames(classes.icon, { [classes.iconActive]: props.view === 'list' })} />
      </IconButton>
    </Card>
  );
});
